@import url(https://fonts.googleapis.com/css?family=Montserrat:200,200i,300,400|Raleway:200,200i,300,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: -webkit-linear-gradient(315deg, #667eea 0%, #764ba2 100%);
  background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.mainAbout{
    color: whitesmoke;

}

.mainAbout_title{
    font-size: 3em;

}
.mainAbout_text, .mainAbout_attention{
    font-size: 2em;
}

.mainAbout_container{
    text-align: justify;
    /* font-family: 'Raleway', sans-serif; */
    font-weight: 200;
}
@media only screen and (max-width: 768px) {
    .mainAbout_title{
        font-size: 2em;

    }
    .mainAbout_text, .mainAbout_attention{
        font-size: 1em;
    }

}
.rs-btn.rs-btn-primary.rs-btn-block{

    height: 6em;
}
.uploadWrapper{
    position: relative;
    width: 100%;
    background: white;
    padding: 4em;
    margin-top: 5%;
    border-radius: 13px;
}

input[type=file]{
    display: none;
}
.mainSection{
    position: relative;
    z-index: 10;
    padding: 1em;
    margin-top: 8em;
    margin-left: 16%;
    margin-right: 16%;
    font-family: 'Montserrat', sans-serif;
}


.backGround{
    background-image: -webkit-linear-gradient(315deg, #667eea 0%, #764ba2 100%);
    background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    position:fixed;
    height: 100%;
    width:100%;
    top: 0;

}

.analyzeSectionBackground{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    background: #150919;
    opacity: 0.4;
    z-index: 20;
}

.analyzeSectionMainContainer{
    position: absolute;
    z-index: 30;
    padding: 1em;
    top: 4em;
    left: 10%;
    right: 10%;
    font-family: 'Montserrat', sans-serif;
    background: whitesmoke;
    border-radius: 1em;

}

.analyzeSectionWrapper{
    opacity: 0;
   /*transition: opacity 1s;*/
}
.analyzeSectionWrapper.show{
    opacity: 1;
}
.chartWFContainer{
    height: 30em;
    overflow-y: auto;
}
.rs-progress-line-inner {
    background: #ff7979 !important;
}

.textareaRawText {
    outline: none;
    resize: none;
    min-width: 300px;
    min-height: 76px;
    max-width: 100%;
    max-height: 100%;
    height: auto;
    display: block;
    width: 100%;
    color: #575757;
    background-color: #fff;
    background-image: none;
    border: 1px solid #e5e5ea;
    -webkit-transition: border-color .3s ease-in-out;
    transition: border-color .3s ease-in-out;
    border-radius: 6px;
    padding: 7px 11px;
    font-size: 14px;
    line-height: 1.42857143;

}

.textareaRawText:focus {
    border-color: #cc00ff;
}

.loadingWrapper{
    background: -webkit-radial-gradient(black, transparent);
    background: radial-gradient(black, transparent);
    height: 100%;

    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    text-align: center;
    color: white;
    opacity: 0;
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
}
.loadingWrapper.show{
    opacity: 1;
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
    z-index: 2000;
}
